<template>
  <v-container
    id="regular-tables-view"
    fluid
    tag="section"
  >
    <material-card
      icon="mdi-clipboard-text"
      icon-small
      color="primary"
      title="Gestin de Marbetes"
    >
      <v-data-table
        :headers="headers"
        :items="lista_marbete"
        sort-by=""
        class="elevation-1"
        :loading="cargando"
        loading-text="Cargando... Espere por favor"
        no-data-text="No existen datos"
        no-results-text="No se encontraron registros coincidentes"
        :search="search"
        :footer-props="{
          showFirstLastPage: true,
          firstIcon: 'mdi-arrow-collapse-left',
          lastIcon: 'mdi-arrow-collapse-right',
          prevIcon: 'mdi-arrow-left',
          nextIcon: 'mdi-arrow-right',
          'items-per-page-text':'Items por pgina'
        }"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Bsqueda"
              single-line
              hide-details
            />
            <v-spacer />
            <v-dialog
              v-model="dialog"
              max-width="500px"
            >
              <!-- <template v-slot:activator="{ on, attrs }">
                <v-btn
                  color="primary"
                  dark
                  class="mb-2"
                  v-bind="attrs"
                  v-on="on"
                >
                  Nuevo Registro
                </v-btn>
              </template> -->
              <v-card>
                <v-card-title>
                  <span class="text-h5">{{ formTitulo }}</span>
                </v-card-title>
                <v-card-text>
                  <v-form ref="form" v-model="valid">
                    <v-container>
                      <!-- {{marbete}} -->
                      <v-row>
                        <v-col
                        cols="12"
                        sm="12"
                        md="6"
                        >
                          <v-text-field
                            v-model="marbete.numeroMarbete"
                            label="N de Marbete"
                            :counter="50"
                            :rules="fieldRules"
                            :autofocus="true"
                            hint="Introduzca el numero"
                            persistent-hint
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="12"
                          md="6"
                        >
                          <v-menu
                            ref="menu1"
                            v-model="menu1"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                          >
                            <template v-slot:activator="{ on, attrs }">
                              <v-text-field
                                v-model="marbete.fecha"
                                label="Fecha Marbete"
                                prepend-icon="mdi-calendar"
                                readonly
                                v-bind="attrs"
                                v-on="on"
                              ></v-text-field>
                            </template>
                            <v-date-picker
                              v-model="marbete.fecha"
                              @input="menu1 = false"
                            >
                            </v-date-picker>
                          </v-menu>
                        </v-col>
                        <v-col
                          cols="12"
                          sm="12"
                          md="12"
                        >
                          <v-text-field
                            v-model="marbete.lugarTrabajo"
                            label="Lugar de Trabajo"
                            :counter="50"
                            :rules="fieldRules"
                            hint="Introduzca el lugar"
                            persistent-hint
                          />
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="close"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    :loading="estadoBoton"
                    text
                    @click="guardar"
                    :disabled="!valid"
                  >
                    Guardar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDetalle"
              max-width="500px"
            >
              <v-card>
                <v-card-title>
                  <span class="text-h4">Detalle de Marbete</span>
                </v-card-title>
                <v-card-text>
                  <div class="text-overline mb-1" style="color: black">
                    <p class="text-h6">N Marbete: {{marbete.numeroMarbete}}</p>
                    <p class="text-h6">Fecha Marbete: {{marbete.fecha}}</p>
                    <p class="text-h6">Carnet Identidad: {{marbete.Afiliado.ci}}</p>
                    <p class="text-h6">Nombre Completo: {{marbete.Afiliado.nombreCompleto}}</p>
                    <p class="text-h6">Departamento: {{marbete.Afiliado.distritoDescripcion}}</p>
                    <p class="text-h6">Profesin: {{marbete.Afiliado.profesionDescripcion}}</p>
                    <p class="text-h6">Matrcula Colegio: {{marbete.Afiliado.numeroInscripcion}}</p>
                    <p class="text-h6">Matrcula Profesional: {{marbete.Afiliado.numeroMinisterio}}</p>
                    <p class="text-h6">Lugar de Trabajo: {{marbete.lugarTrabajo}}</p>
                    
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    text
                    @click="closeDetalle"
                  >
                    Cerrar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>


            <v-dialog
              v-model="dialogDelete"
              max-width="500px"
            >
              <v-card>
                <v-card-title class="text-h5">
                  Est seguro de eliminar el registro?
                </v-card-title>
                <v-spacer />
                <v-card-subtitle class="text-h6">
                  {{ marbete.Afiliado.ci + ' - ' + marbete.Afiliado.nombreCompleto }}
                </v-card-subtitle>
                <v-card-actions>
                  <v-spacer />
                  <v-btn
                    color="blue darken-1"
                    text
                    @click="closeEliminar"
                  >
                    Cancelar
                  </v-btn>
                  <v-btn
                    color="red darken-1"
                    text
                    @click="eliminarConfirm"
                  >
                    Eliminar
                  </v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>
          </v-toolbar>
        </template>
        <template v-slot:[`item.acciones`]="{ item }">
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary darken-2"
            @click="detalle(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-text
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="primary"
            @click="imprimir()"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-printer
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="yellow darken-3"
            @click="editar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-pencil
            </v-icon>
          </v-btn>
          <v-btn
            class="ma-1"
            fab
            dark
            x-small
            color="red darken-2"
            @click="eliminar(item)"
          >
            <v-icon
              dark
              small
              class="ma-2"
            >
              mdi-delete
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </material-card>
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
      :color="color"
    >
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color=""
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          Cerrar
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
// import * as profesionService from "../services/profesionService"
// import * as personaService from "../services/personaService"
import * as marbeteService from "../services/marbeteService"
// import * as kardexService from "../services/kardexService"
// import * as distritoService from "../services/distritoService"
import jsPDF from 'jspdf'
import autoTable from 'jspdf-autotable'

  export default {
    data () {
      return {
        estadoBoton: false,
        dialog: false,
        dialogDelete: false,
        dialogDetalle: false,
        lista_marbete: [],
        lista_kardex: [],
        cargando: true,
        marbete: {
          Afiliado: {ci: '', nombreCompleto: ''}
        },
        kardex: {
          Persona: {ci:'', paterno:'', materno:'', nombre:''}
        },
        search: '',
        headers: [
          {
            text: 'ID',
            align: 'start',
            sortable: false,
            value: 'id',
          },
          { text: 'Departamento', value: 'Afiliado.distritoDescripcion' },
          { text: 'N M.', value: 'numeroMarbete' },
          { text: 'C.I.', value: 'Afiliado.ci' },
          { text: 'Nombre Completo', value: 'Afiliado.nombreCompleto' },
          { text: 'Profesin', value: 'Afiliado.profesionDescripcion' },
          { text: 'Mat. Col.', value: 'Afiliado.numeroInscripcion' },
          { text: 'Mat. Prof.', value: 'Afiliado.numeroMinisterio' },
          { text: 'Lugar Trab.', value: 'lugarTrabajo' },
          // { text: 'Fecha', value: 'fecha' },
          { text: 'Acciones', value: 'acciones', sortable: false, align: 'center' },
        ],
        editedIndex: -1,
        defaultMarbete: {
          Afiliado: {ci: '', nombreCompleto: ''}
        },
        color: '',
        snackbar: false,
        text: '',
        timeout: 3000,
        valid: true,
        fieldRules: [
          v => !!v || 'El campo es requerido',
          v => (v && v.length <= 50) || 'El campo debe ser menor a 50 caracteres',
        ],
        menu1: false,
        menu2: false,
        menu3: false,
      }
    },
    computed: {
      formTitulo () {
        return this.editedIndex === -1 ? 'Nuevo Registro' : 'Editar Registro'
      },
    },
    watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeEliminar()
      },
      dialogDetalle (val) {
        val || this.closeDetalle()
      },
    },
    async mounted () {
      // let datos = await marbeteService.listar()
      // console.log(datos);
      // this.lista_marbete = datos
      // this.cargando = false
      this.obtener()
    },
    methods: {
      async obtener () {
        const res = await marbeteService.listar()
        // console.log(datos)
        this.lista_marbete = res.data
        this.cargando = false
      },
      // async obtenerDistrito () {
      //   const res = await distritoService.listar()
      //   // console.log(res)
      //   this.lista_distrito = res.data
      //   this.cargando = false
      // },
      // async obtenerProfesion () {
      //   const res = await profesionService.listar()
      //   // console.log(res)
      //   this.lista_profesion = res.data
      //   this.cargando = false
      // },
      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.marbete = Object.assign({}, this.defaultMarbete)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      closeDetalle () {
        this.dialogDetalle = false
        this.$nextTick(() => {
          this.marbete = Object.assign({}, this.defaultMarbete)
          this.editedIndex = -1
        })
        this.estadoBoton = false
      },
      async guardar () {
        let res = null
        this.validate();
        this.estadoBoton = true
        if (this.editedIndex > -1) {
          const mar = this.lista_marbete[this.editedIndex]
          res = await marbeteService.modificar(this.marbete, mar.id)
          Object.assign(this.lista_marbete[this.editedIndex], this.marbete)
        } else {
          res = await marbeteService.guardar(this.marbete)
          // console.log(res);
          this.lista_marbete.push(this.marbete)
        }
        if(res.status == 200){
          this.text = "Registro guardado"
          this.snackbar = true
          this.color = "green"
        } else {
          this.text = res.data.message
          this.snackbar = true
          this.color = "dark-red"
        }
        this.obtener()
        this.estadoBoton = false
        this.close()
      },
      editar (item) {
        this.editedIndex = this.lista_marbete.indexOf(item)
        this.marbete = Object.assign({}, item)
        this.dialog = true
      },
      detalle (item) {
        this.editedIndex = this.lista_marbete.indexOf(item)
        this.marbete = Object.assign({}, item)
        this.dialogDetalle = true
      },
      eliminar (item) {
        this.editedIndex = this.lista_marbete.indexOf(item)
        this.marbete = Object.assign({}, item)
        this.dialogDelete = true
      },
      async eliminarConfirm () {
        try {
          const mar = this.lista_marbete[this.editedIndex]
          const res = await marbeteService.eliminar(mar.id)
          if(res.status == 200){
            this.text = res.data.message
            this.snackbar = true
            this.color = "green"
            this.lista_marbete.splice(this.editedIndex, 1)
          } else {
            this.text = res.data.message
            this.snackbar = true
            this.color = "dark-red"
          }
          // if (!res.error) {
          //   this.lista_marbete.splice(this.editedIndex, 1)
          // }
          this.closeEliminar()
        } catch (error) {
          console.log(error);
        }
      },
      closeEliminar () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.marbete = Object.assign({}, this.defaultMarbete)
          this.editedIndex = -1
        })
      },
      validate() {
        this.$refs.form.validate()
      },
      imprimir(){
        // let data = [
        //   {
        //       id: 2,
        //       paterno: "Perez",
        //       materno: "Lopez",
        //       nombre: "Juan",
        //       ci: "1234567",
        //   },
        //   {
        //       id: 1,
        //       paterno: "Lahor",
        //       materno: "Salguero",
        //       nombre: "Miguel",
        //       ci: "7483752",
        //   }
        // ]
        let data = this.lista_marbete
        console.log(this.lista_marbete);
        let info = []

        data.forEach((element, index, array) => {
          info.push([index+1, element.id, element.numeroMarbete, element.lugarTrabajo, element.nombre, element.ci])
        });


        let doc = new jsPDF()

        doc.autoTable({
          head: [['1', '2', '3', '4', '5', '6',]],
          body: info
        })

        window.open(doc.output('bloburl'))
      }
    },
  }
</script>
